.container {
  width: 100%;

  border: 1px solid #ccc;
  border-radius: 10px;
  margin-bottom: 30px;
}

.header_list {
  background-color: #dcecfd;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 5px 10px 5px 10px;
}
.foter_list {
  padding: 7px 10px 5px 10px;
  background-color: #dcecfd;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.item {
  padding: 12px;
  border-bottom: 1px solid #eee;
}
.item_new {
  background-color: rgba(3, 252, 252, 0.103);
}

.header_list,
.foter_list,
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.title {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.size {
  font-size: 14px;
  color: #777;
}

.description {
  margin: 6px 0 10px;
  color: #555;
}

/* Кнопки */
.buttons {
  display: flex;
  gap: 10px;
}

.playButton {
  background: #007bff;
  color: white;
  padding: 7px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.deleteButton {
  background: #ff4444;
  color: white;
  padding: 7px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

/* POPUP */
.popupOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.6);

  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.popup {
  position: relative;
  background: rgb(0, 0, 0);
  padding: 50px 10px 5px 10px;

  border-radius: 10px;
  max-width: 400px;
}

.image {
  width: 100%;
  border-radius: 8px;
}

.closeBtn {
  position: absolute;
  top: 10px;
  right: 10px;

  background: #ccc;
  border: none;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  cursor: pointer;
  font-size: 16px;
}

@media (any-hover: hover) {
  .playButton:hover,
  .deleteButton:hover,
  .closeBtn:hover,
  .addImage_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.playButton:active,
.deleteButton:active,
.closeBtn:active,
.addImage_btn:active {
  opacity: 0.5;
}
