/*------------------------------------ popup -------------------------- */
.popup_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_contnent_wrapper {
  max-width: 300px;
  background-color: var(--color-3);
  padding: 70px 30px 30px 30px;
  border-radius: 10px;
  position: relative;
  font-size: 16px;
}
.popup_close_btn {
  background-color: var(--color-2);
  color: var(--color-3);
  padding: 5px 10px;
  border-radius: 10px;
  position: absolute;
  top: 20px;
  right: 20px;
}
.popup_input {
  width: 100%;
  background-color: var(--color-12);
  border: none;
  height: 37px;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 5px;
}
.fromAdvertising_wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}
.fromAdvertising_btn {
  width: 25px;
  height: 25px;
  border: solid 1px var(--color-1);
  border-radius: 5px;
  background-color: var(--color-3);
}

.fromAdvertising_btn_active {
  background-color: var(--color-22);
}
.ownСlient_btn_active {
  background-color: var(--color-21);
}
.potentialClient_btn_active {
  background-color: var(--color-20);
}
.popup_add_btn_wrapper {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.popup_add_btn {
  background-color: var(--color-6);
  color: var(--color-3);
  padding: 7px 35px;
  border-radius: 10px;
}
/*------- /popup -------------------------- */

@media (any-hover: hover) {
  .popup_close_btn:hover,
  .popup_add_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.popup_close_btn:active,
.popup_add_btn:active {
  opacity: 0.5;
}
