/* --------- header -------- */
.header_wrapper {
  background-color: var(--color-2);
  padding: 2px 15px;
  position: relative;
  height: 50px;
  display: flex;
  align-items: center;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header_title {
  width: 100%;
  text-align: center;
}

.header_back_btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}
.selected_list {
  background-color: var(--color-11);
  padding: 5px 5px 5px 8px;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: 100%;
  border-radius: 5px;
  font-size: 16px;
  color: var(--color-3);
  max-height: calc(100vh - 50px);
  overflow: auto;
}
.selected_list_item {
  padding: 3px;
  margin-bottom: 3px;
}
.selected_list_item.active {
  opacity: 0.8;
}
/* --------- header -------- */

/* ---------- */
.content_wrapper {
  padding: 0px 3px 10px 3px;
  margin-top: 60px;
  position: relative;
  overflow: hidden;
}
.statistics_date {
  line-height: 1;
  display: flex;
  align-items: center;
  color: var(--color-3);
  padding-left: 5px;
  font-size: 14px;
  height: 20px;
  background-color: var(--color-6);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.line_control {
  height: calc(100% - 34px);
  width: 2px;
  background-color: var(--color-1_2);
  position: absolute;
  top: 20px;
  left: 235px;

  transform: rotateY(180deg);
  z-index: 2;
}
.statistics_item {
  display: flex;
  align-items: center;
  line-height: 1;
  background-color: var(--color-4);
  padding-left: 3px;
  font-size: 13px;
  margin-bottom: 4px;

  overflow-y: hidden;
  overflow-x: auto;
}
.statistics_item:nth-child(2n) {
  background-color: var(--color-12);
}
.statistics_client_name {
  margin-right: 5px;
  flex-basis: 155px;
  flex-shrink: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.statistics_value {
  padding: 1px;
  white-space: nowrap;
}
.statistics_sleep_value {
  background-color: var(--color-1);
}
.statistics_water_value {
  background-color: var(--color-16);
}
.statistics_not_norm {
  background-color: var(--color-10);
}

/* ------- selected ------------- */
.selected_wrapper {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  display: flex;
  align-items: center;
  gap: 5px;
}
.selected_day_wrapper {
  position: relative;
}
.selected_day {
  background-color: var(--color-11);
  color: var(--color-3);
  padding: 5px 5px 5px 8px;
  min-height: 35px;
  border-radius: 5px;
  display: flex;
  font-size: 12px;
  width: 120px;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  position: relative;
}

.selected_day svg {
  transition: transform 0.1s;
}
/* ------- /selected ------------- */
/* -------------------------------------- */

@media (any-hover: hover) {
  .select_statistics_btn:hover,
  .interaction_item:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.select_statistics_btn:active,
.interaction_item:active,
.header_back_btn:active {
  opacity: 0.5;
}
