.close_btn {
  padding: 0;
  width: 40px;
  height: 40px;
  background-color: var(--color-2);
  border-radius: 5px;
  position: relative;
}
.line {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 35px;
  height: 3px;
  border-radius: 2px;
  background-color: var(--color-3);
}
.line_1 {
  transform: translate(-50%, -50%) rotate(45deg);
}
.line_2 {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@media (any-hover: hover) {
  .close_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.close_btn:active {
  opacity: 0.5;
}
