/* --------- header -------- */
.header_wrapper {
  background-color: var(--color-2);
  padding: 2px 15px;
  position: relative;
  height: 50px;
  display: flex;
  align-items: center;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 101;
}
.header_title {
  width: 100%;
  text-align: center;
}

.header_back_btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}
/* --------- header -------- */

/* ---------- */
.content_wrapper {
  padding: 10px 3px 10px 3px;
  margin-top: 50px;
  position: relative;
}

.statistics_item {
  display: flex;
  align-items: center;
  line-height: 1;
  background-color: var(--color-4);

  padding-left: 3px;
  padding-top: 5px;
  padding-bottom: 5px;

  font-size: 13px;
  margin-bottom: 4px;

  overflow-x: auto;
}
.statistics_item:nth-child(2n) {
  background-color: var(--color-12);
}
.statistics_client_name_number {
  font-size: 10px;
}
.statistics_client_name {
  flex-basis: 145px;
  flex-shrink: 0;
  padding-right: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* -------------------------------------- */
.statistics_conclusion_wrapper {
  border-right: 2px solid var(--color-1);
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  gap: 3px;
  color: var(--color-2);
  font-weight: 500;

  flex-basis: 50px;
  flex-shrink: 0;
}

.measurements_wrapper {
  display: flex;
  flex-direction: column;
  gap: 3px;

  white-space: nowrap;
  color: var(--color-2);
  font-weight: 500;
  margin-right: 10px;
}
.statistics_conclusion_measurements,
.statistics_conclusion_kg,
.measurement_value,
.measurements_kg {
  background-color: var(--color-3);
  border-radius: 2px;
  position: relative;
  padding: 3px;
}

.statistics_true {
  position: absolute;
  top: 0px;
  left: -10px;
  color: var(--color-2);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.statistics_unsatisfactory {
  color: var(--color-10);
  font-weight: 500;
}
.statistics_nan {
  color: var(--color-10);
  font-weight: 500;
  text-align: center;
}
.oneMeasurements_wrapper {
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.oneMeasurements_item {
  background-color: var(--color-3);
  padding: 3px;
  border-radius: 2px;
}

.calculate_result_btn {
  padding: 10px;
  border-radius: 10px;
  margin-left: 10px;
  margin-top: 15px;
  margin-bottom: 35px;
  background-color: var(--color-1);
  color: var(--color-3);
  min-width: 250px;
}
/* ------------------------------------------- */

@media (any-hover: hover) {
  .calculate_result_btn:hover,
  .popup_close_btn:hover,
  .statistics_item_active:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.calculate_result_btn:active,
.popup_close_btn:active,
.statistics_item_active:active,
.header_back_btn:active {
  opacity: 0.5;
}
