.header_wrapper {
  background-color: var(--color-2);
  height: 50px;
  display: flex;
  align-items: center;
  padding: 2px 15px;
  justify-content: space-between;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 110;
}
.header_back_btn {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}

@media (any-hover: hover) {
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.header_back_btn:active {
  opacity: 0.5;
}
