/*------------------------------------ popup -------------------------- */
.popup_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
}
.popup_contnent_wrapper {
  width: 100%;
  max-width: 380px;
  background-color: var(--color-3);
  border-radius: 10px;
  position: relative;
  font-size: 16px;
  padding: 70px 0px 10px 0px;
}
.popup_contnent_fon {
  padding: 10px 10px 20px 10px;
  background-color: var(--color-12);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.popup_close_btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
}
.popup_input_wrapper {
  position: relative;
  margin-bottom: 15px;
}
.popup_input {
  width: 100%;
  background-color: var(--color-3);
  border: 2px solid var(--color-2);
  height: 37px;
  border-radius: 5px;
  padding: 5px;
}

.popup_input_error {
  border: 2px solid var(--color-10);
}

.popup_add_btn_wrapper {
  display: flex;
  justify-content: right;
  margin-top: 50px;
}
.popup_add_btn {
  background-color: var(--color-2);
  color: var(--color-3);
  padding: 7px 35px;
  border-radius: 10px;
}

/*------- /popup -------------------------- */

@media (any-hover: hover) {
  .popup_add_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.popup_add_btn:active {
  opacity: 0.5;
}
