.block_title {
  font-size: 16px;
}
.addGroup_wrapper {
  padding: 20px 10px;
  background-color: var(--color-7);
  border-radius: 10px;
  margin-bottom: 50px;
}
.addGroup_input {
  width: 100%;
  height: 40px;
  border-radius: 10px;
  border: 0px;
  padding: 5px;
  margin-bottom: 20px;
}
.addGroup_btn {
  padding: 10px 20px;
  background-color: var(--color-6);
  color: var(--color-3);
  border-radius: 10px;
}

@media (any-hover: hover) {
  .addGroup_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .addGroup_btn:active {
    opacity: 0.5;
  }
}
