.menuDay_wrapper {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.numberDay_wrapper {
  display: flex;
  gap: 10px;
  padding: 10px;
  align-items: center;
  background-color: var(--color-11);
  border-radius: 10px;
  margin-bottom: 20px;
}
.numberDay_title {
  color: var(--color-3);
}
.input_NumberDay {
  height: 35px;
  width: 100px;
  border-radius: 10px;
  padding: 5px;
}

.save_btn {
  padding: 10px;
  width: 90px;
  border-radius: 10px;
  background-color: var(--color-2);
  color: var(--color-3);
  position: sticky;
  bottom: 20px;
  left: calc(100% - 95px);
  cursor: pointer;
}

@media (any-hover: hover) {
  .save_btn:hover {
    opacity: 0.8;
  }
}
.save_btn:active {
  opacity: 0.5;
}
