.header_wrapper {
  background-color: var(--color-2);
  height: 50px;
  display: flex;
  align-items: center;
  padding: 2px 10px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header_back_btn {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}

.selected_wrapper {
  display: flex;
  align-items: center;
  gap: 5px;
}
.selected_day_wrapper {
  position: relative;
}
.selected_day {
  background-color: var(--color-11);
  color: var(--color-3);
  padding: 5px 5px 5px 8px;
  min-height: 35px;
  border-radius: 5px;
  display: flex;
  font-size: 16px;
  width: 115px;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  position: relative;
}

.selected_day svg {
  transition: transform 0.1s;
}

.selected_list {
  background-color: var(--color-11);
  padding: 5px 5px 5px 8px;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: 100%;
  border-radius: 5px;
  font-size: 16px;
  color: var(--color-3);
  max-height: calc(100vh - 150px);
  overflow: auto;
}
.selected_list_item {
  padding: 3px;
  margin-bottom: 3px;
}
.selected_list_item.active {
  opacity: 0.8;
}
.show {
  transform: rotate(180deg);
}
.opacity_0 {
  opacity: 0;
}
/* -------------------------------------- */
@media (any-hover: hover) {
  .interaction_item:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}
.interaction_item:active,
.header_back_btn:active {
  opacity: 0.5;
}
