.productsList_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--color-3);
  width: 100%;
  padding: 0px 5px 30px 5px;
  height: 100%;
  overflow-y: auto;
  z-index: 120;
}
.productsList_text {
  padding: 5px;
  background-color: var(--color-4);
  border-radius: 5px;
}
.productsList_text:not(:last-child) {
  margin-bottom: 5px;
}
.productsList_text_active {
  opacity: 0.3;
}

.productsList_top {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  padding-top: 10px;
  margin-bottom: 10px;
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: var(--color-3);
}
.input_search {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  border: 2px solid var(--color-18);
}

/* ------------------------------------- */
@media (any-hover: hover) {
  .productsList_text:not(.productsList_text_active):hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.productsList_text:not(.productsList_text_active):active {
  opacity: 0.5;
}
