/* --------- header -------- */
.header_wrapper {
  background-color: var(--color-2);
  padding: 2px 15px;
  position: relative;
  height: 50px;
  display: flex;
  align-items: center;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header_title {
  width: 100%;
  text-align: center;
}

.header_back_btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}
/* --------- header -------- */
.content_wrapper {
  padding: 60px 10px 10px 10px;
}

/*--------- control_button ------- */
.control_button_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.control_btn {
  font-size: 16px;
  height: 30px;
  width: 145px;
  border-radius: 5px;
  color: var(--color-3);
}
.add_allAccess {
  background-color: var(--color-1);
}
.delete_allAccess {
  background-color: var(--color-10);
}
/*--------- /control_button ------- */
/*--------- client_wrapper ------- */
.client_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-12);
  border-radius: 10px;
  padding: 5px 10px;
  margin-bottom: 10px;
}

.client_title_wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
}
.client_tel {
  font-size: 12px;
  line-height: 1.3;
  padding-right: 5px;
  color: var(--color-1_1);
}

.input_search {
  height: 40px;
  width: 100%;
  border-radius: 5px;
  border: 2px solid var(--color-18);
  padding: 5px;
  margin-bottom: 15px;
}
/*--------- /client_wrapper ------- */

@media (any-hover: hover) {
  .client_add_btn:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.client_add_btn:active,
.header_back_btn:active {
  opacity: 0.5;
}
