/*------------------------------------ popup -------------------------- */
.popup_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  overflow-y: auto;
  background-color: var(--color-3);
}

.close_btn {
  position: fixed;
  top: 10px;
  right: 15px;
  z-index: 10;
}
.popup_contnent_wrapper {
  padding: 50px 5px 20px 5px;
}
.check_block,
.basket_block,
.ingredient_block {
  padding: 10px;
  background-color: var(--color-7);
  margin-bottom: 15px;
}
.input_wrapper {
  position: relative;
  margin-bottom: 15px;
}
.input_name {
  width: 100%;
  height: 40px;
  padding: 5px;
}
.unit_wrapper {
  position: relative;
  display: flex;
}
.roundingIngredient_wrapper {
  display: flex;
  position: relative;
}
.input_unit {
  height: 40px;
  width: 100px;
  padding: 5px;
}

.unit_list_wrapper {
  position: absolute;
  top: 105%;
  left: 0;
  width: 100%;
  background-color: var(--color-19);
  border: 2px solid var(--color-2);
  padding: 5px;
  z-index: 2;
}
.units_text {
  padding: 5px;
  background-color: var(--color-3);
}
.units_text_active {
  background-color: var(--color-13);
}

.units_text:not(:last-child) {
  margin-bottom: 5px;
}

.input_valueCalculateForProduct,
.input_valueCalculateForIngredient {
  height: 40px;
  width: 60px;
  padding: 5px;
}
.table_macros_default {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 40px 40px;
  border: 1px solid;
}
.input_table_value {
  width: 100%;
  height: 100%;
  padding: 2px;
}

.product_name {
  background-color: var(--color-3);
  flex-grow: 1;
  min-height: 40px;
  display: flex;
  align-items: center;
  padding: 5px;
}

.check_tex {
  background-color: var(--color-3);
  min-height: 40px;
  padding: 5px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.input_value {
  height: 40px;
  width: 70px;
  border: 2px solid var(--color-2);
  padding: 5px;
}
.onChange_value_btn {
  background-color: var(--color-2);
  width: 40px;
  color: var(--color-3);
  font-size: 20px;
}
.onChange_value_btn_minus {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.onChange_value_btn_plus {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.add_btn_wrapper {
  display: flex;
  justify-content: flex-end;
}
.add_btn {
  padding: 5px 25px;
  background-color: var(--color-2);
  color: var(--color-3);
  border-radius: 5px;
  font-size: 18px;
}
.roundingIngredient {
  background-color: var(--color-3);
  border: solid 1px;
  width: 100%;
  padding: 5px;
  display: flex;
  align-items: center;
}
/*------- /popup -------------------------- */

@media (any-hover: hover) {
  .add_btn:hover,
  .onChange_value_btn:hover,
  .units_text:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}
.add_btn:active,
.onChange_value_btn:active,
.units_text:active {
  opacity: 0.5;
}
