.wrapper {
  max-width: 500px;
  margin: 20px auto;
  font-family: sans-serif;
}
.title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 5px;
}

.button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid #ccc;
  background: #f7f7f7;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.button:hover {
  background: #eaeaea;
}

/* --- Dropdown with smooth animation --- */
.dropdown {
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  border: 1px solid transparent;
  border-radius: 6px;
  transition: max-height 0.35s ease, opacity 0.25s ease, border 0.3s;
}

.open {
  max-height: 500px; /* більше за висоту списку */
  opacity: 1;
  border: 1px solid #ccc;
}

.closed {
  max-height: 0;
  opacity: 0;
  border: 1px solid transparent;
}

.item {
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  background: white;
  transition: background 0.2s ease;
}

.item:hover {
  background: #f5f5f5;
}

.active {
  background: #dff1ff;
}

.description {
  margin: 4px 0 0 0;
  font-size: 14px;
  color: #555;
}

.selectedBox {
  margin-top: 10px;
}

.video {
  width: 100%;
  border-radius: 8px;
}

.removeBtn {
  margin-top: 10px;
  width: 100%;
  padding: 8px;
  cursor: pointer;
  background: #ffdddd;
  border: 1px solid #ff8080;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.removeBtn:hover {
  background: #ffcccc;
}

.menu_header {
  height: 73px;
  background-color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: relative;
  overflow: hidden;
}

.menu_header_image {
  background-size: cover;
  background-position: center center;

  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
}
