/* --------- header -------- */
.header_wrapper {
  background-color: var(--color-2);
  height: 50px;
  display: flex;
  align-items: center;
  padding: 2px 15px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header_back_btn {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}
/* --------- header -------- */

.content_wrapper {
  padding: 70px 15px 20px 15px;
}
.info_wrapper {
  border: 1px solid var(--color-1);
  padding: 10px 10px 0px 10px;
  background-color: var(--color-4);
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
}
.edit_icon_svg {
  fill: var(--color-6);
  height: 25px;
  width: 25px;
  position: absolute;
  top: 10px;
  right: 10px;
}
.info_item_wrapper {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 10px;
}
.info_item_wrapper svg {
  height: 20px;
  width: 20px;
}
.info_item_title {
  opacity: 0.6;
}
.info_item {
  border-radius: 10px;
}
.info_item_fromAdvertising {
  color: var(--color-3);
  background-color: var(--color-22);
  font-weight: 600;
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 5px;
}
.info_item_id {
  font-size: 13px;
  position: relative;
}
.alertCopyTrue {
  position: absolute;
  top: 0;
  left: 40px;
  background-color: var(--color-1);
  color: var(--color-3);
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 3px;
}
.info_item_link {
  color: var(--color-14);
  text-decoration: underline;
}
.info_item_time {
  font-size: 10px;
  opacity: 0.6;
}

/* ------ 2 -------- */
.active_control_wrapper {
  border: 1px solid var(--color-1);
  padding: 10px 10px;
  background-color: var(--color-4);
  border-radius: 10px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
}
.active_control_btn {
  border: 1px solid var(--color-1);
  width: 25px;
  height: 25px;
  border-radius: 5px;
  background-color: var(--color-3);
}
.active_control_btn_active {
  background-color: var(--color-1);
}
/* ------ /2 -------- */
/* ------ 3 -------- */
.accessGroups_wrapper {
  border: 1px solid var(--color-1);
  padding: 10px 10px 0px 10px;
  background-color: var(--color-4);
  border-radius: 10px;
  margin-bottom: 20px;
}
.accessGroups_item {
  background-color: var(--color-3);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3px 10px 3px 10px;

  margin-bottom: 7px;
}
.accessGroups_item_active {
  width: 14px;
  height: 14px;
  background-color: var(--color-1);
  border-radius: 50%;
}
.withoutAccessGroups_text {
  color: var(--color-10);
  margin-bottom: 20px;
  background-color: var(--color-4);
  border: 1px solid var(--color-1);
  padding: 10px;
  border-radius: 10px;
}

/* ------ /3 -------- */

@media (any-hover: hover) {
  .info_item_id:hover,
  .accessGroups_delete_btn:hover,
  .edit_icon_svg:hover,
  .info_item_link:hover,
  .active_control_btn:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.active_control_btn:active,
.info_item_id:active,
.accessGroups_delete_btn:active,
.info_item_link:active,
.edit_icon_svg:active,
.header_back_btn:active {
  opacity: 0.5;
}
