.item_wrapper {
  display: grid;
  grid-template-columns: 120px 40px 40px 40px 40px 40px;
  gap: 4px;
  background-color: var(--color-4);

  border-radius: 5px;
  margin-bottom: 10px;
  position: relative;
}

.viewTime {
  position: absolute;
  top: -7px;
  left: 0;
  font-size: 9px;
  line-height: 1;
  padding-left: 3px;
  padding-right: 3px;
  border-radius: 3px;
  display: flex;
  gap: 2px;
}

.viewTime_item {
  background-color: var(--color-3);
  padding-left: 3px;
  padding-right: 3px;
  border-radius: 3px;
}
.timeViewMilliseconds {
  color: var(--color-1);
}
.counterNull {
  color: var(--color-10);
}

.fullName {
  display: flex;
  align-items: center;
  padding-left: 5px;
  padding-right: 10px;
  font-size: 13px;
}
.counter_item_wrapper {
  border: solid 1px var(--color-1);

  background-color: var(--color-3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  padding-top: 5px;
  height: 100%;
  min-width: 30px;
}
.counter_item_wrapper_noActive {
  opacity: 0.2;
}
.value {
  font-size: 10px;
}

/* ------ result ------------ */
.timeViewMillisecondsAllTime {
  background-color: var(--color-6);
  color: var(--color-3);
  padding: 5px 10px;
  border-radius: 5px;
}
.result_wrapper {
  background-color: var(--color-1);
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-radius: 5px;
  display: flex;
  gap: 4px;
  margin-bottom: 10px;
}
.result_item_wrapper {
  border: solid 1px var(--color-1);
  border-radius: 5px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 5px;

  background-color: var(--color-3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-width: 40px;
  height: 50px;
  position: relative;
}

.timeCounter {
  font-size: 11px;
}
.restart_svg {
  position: absolute;
  top: 1px;
  right: 1px;
  opacity: 0.3;
}
.result_item_count {
  position: absolute;
  top: 0;
  left: 3px;
  font-size: 11px;
  line-height: 1;
}
/* ------ /result ------------ */

/*------------- popup  --------------------*/
.popup_fon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_content_wrapper {
  background-color: var(--color-3);
  padding: 40px;
  border-radius: 10px;
  position: relative;
}

.popup_content {
  margin-bottom: 50px;
}
.popup_item_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 240px;
  margin-bottom: 20px;
}
.popup_itemSelect {
  width: 25px;
  height: 25px;
  border: solid 1px var(--color-1);
  border-radius: 5px;
}
.popup_itemSelect_active {
  background-color: var(--color-1);
}
.popup_btn_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.popup_btn {
  background-color: var(--color-1);
  color: var(--color-3);
  padding: 5px;
  border-radius: 5px;
}
.popup_reset_btn {
  background-color: var(--color-10);
}
/*------------- /popup -------------------- */

@media (any-hover: hover) {
  .popup_itemSelect:hover,
  .popup_btn:hover,
  .button:hover,
  .item_wrapper:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.popup_itemSelect:active,
.button:active,
.popup_btn:active,
.item_wrapper:active {
  opacity: 0.5;
}
