.menu_info_wrapper {
  margin-bottom: 20px;
}
.getHistory_btn {
  border-radius: 10px;
  background-color: var(--color-5);
  padding: 10px;
}

/* ------------------------------------- */
.popupHistory_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popupHistory_content_wrapper {
  background-color: var(--color-3);
  width: 90%;
  padding: 20px 20px 20px 20px;
  min-height: 200px;
  max-height: 95vh;
  overflow: auto;
  position: relative;
  border-radius: 10px;
}
.popupHistory_list_wrapper {
  position: relative;
}
.popupHistory_close_btn {
  position: sticky;
  top: 0px;
  left: 100%;
  padding: 3px 10px;
  border-radius: 10px;
  background-color: var(--color-1);
  color: var(--color-3);
  margin-bottom: 30px;
}
.history_group_name {
  background-color: var(--color-4);
  padding: 5px 10px;
  border-radius: 10px;
  margin-bottom: 10px;
}
/* ------------------------------------- */

/* ---------------------------------- */
@media (any-hover: hover) {
  .popupHistory_close_btn:hover,
  .getHistory_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.popupHistory_close_btn:active,
.getHistory_btn:active {
  opacity: 0.5;
}
