.header_wrapper {
  background-color: var(--color-2);
  padding: 10px 15px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  width: 100%;
  z-index: 3;
}
.header_link {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16;
  font-weight: 600;
}
.content_wrapper {
  padding: 60px 10px 10px 10px;
}
.client_wrapper {
  padding: 7px 5px;
  background-color: var(--color-12);
  border-radius: 10px;
  margin-bottom: 10px;
  position: relative;
}

.client_new {
  position: absolute;
  top: 0px;
  left: 3px;
  background-color: var(--color-2);
  font-size: 10px;
  padding: 1px 5px 1px 5px;
  line-height: 1;
  border-radius: 5px;
  color: var(--color-3);
  z-index: 2;
}

.top_block {
  display: flex;
  align-items: center;
  gap: 10px;
}

.client_info {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;

  background-color: var(--color-4);
  border-radius: 10px;

  width: 100%;
}
.client_info_fromAdvertising {
  background-color: var(--color-22);
  color: var(--color-3);
}
.client_info_ownСlient {
  background-color: var(--color-21);
  color: var(--color-3);
}
.client_info_potentialClient {
  background-color: var(--color-20);
  color: var(--color-3);
}
.client_tel {
  color: var(--color-1_1);
  font-size: 12px;
  line-height: 1.3;
}

.delete_client_wrapper {
  position: relative;
  border-radius: 10px;
  height: 100%;
  background-color: var(--color-4);
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.accessGroup_item {
  background-color: var(--color-3);
  margin-top: 7px;
  padding: 3px 10px 3px 10px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accessGroups_item_active {
  width: 14px;
  height: 14px;
  background-color: var(--color-1);
  border-radius: 50%;
}
.input_search {
  height: 40px;
  width: 100%;
  border-radius: 5px;
  border: 2px solid var(--color-18);
  padding: 5px;
  margin-bottom: 15px;
}

@media (any-hover: hover) {
  .delete_client_wrapper:hover,
  .popupGroupsList_close_btn:hover,
  .popupGroupsList_item:hover,
  .accessGroup_delete_btn:hover,
  .client_info:hover,
  .popup_close_btn:hover,
  .interaction_item:hover,
  .popup_add_btn:hover,
  .header_link:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.delete_client_wrapper:active,
.popupGroupsList_close_btn:active,
.popupGroupsList_item:active,
.accessGroup_delete_btn:active,
.client_info:active,
.popup_close_btn:active,
.popup_add_btn:active,
.interaction_item:active,
.header_link:active {
  opacity: 0.5;
}
