/* --------- header -------- */
.header_wrapper {
  background-color: var(--color-2);
  padding: 2px 10px;

  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header_back_btn {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}
.interaction_link {
  display: flex;
  align-items: center;
}
/* --------- header -------- */
.content_wrapper {
  padding: 60px 10px 10px 10px;
}

/*--------- control_button ------- */
.control_button_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.control_btn {
  font-size: 16px;
  height: 30px;
  width: 145px;
  border-radius: 5px;
  color: var(--color-3);
}
.add_allAccess {
  background-color: var(--color-1);
}
.delete_allAccess {
  background-color: var(--color-10);
}
/*--------- /control_button ------- */
/*--------- client_wrapper ------- */
.client_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-12);
  border-radius: 10px;
  padding: 5px 10px;
  margin-bottom: 10px;
  gap: 10px;
  position: relative;
}
.client_wrapper_fromAdvertising {
  background-color: var(--color-22);
  color: var(--color-3);
}
.client_wrapper_ownСlient {
  background-color: var(--color-21);
  color: var(--color-3);
}
.client_wrapper_potentialClient {
  background-color: var(--color-20);
  color: var(--color-3);
}

.client_link {
  padding-top: 10px;
  padding-bottom: 10px;
}
.client_btn_wrapper {
  display: flex;
  gap: 15px;
}
.copy_clientId_btn {
  background-color: var(--color-1);
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 3px;
  color: var(--color-3);
  position: relative;
}
.alertCopyTrue {
  position: absolute;
  top: 2px;
  background-color: var(--color-1);
  right: calc(100% + 15px);
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 3px;
  z-index: 2;
}

.change_selected_btn {
  width: 25px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid var(--color-1);
  background-color: var(--color-3);
}
.change_selected_btn_active {
  background-color: var(--color-1);
}
/*--------- /client_wrapper ------- */

@media (any-hover: hover) {
  .copy_clientId_btn:hover,
  .control_btn:hover,
  .client_delete_btn:hover,
  .change_selected_btn:hover,
  .interaction_link_svg:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.copy_clientId_btn:active,
.control_btn:active,
.client_delete_btn:active,
.change_selected_btn:active,
.interaction_link_svg:active,
.header_back_btn:active {
  opacity: 0.5;
}
