.page_wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content_wrapper {
  border-radius: 10px;
  padding: 20px;
  border: solid 1px var(--color-1);
  display: flex;
  flex-direction: column;
  gap: 20px;

  background-color: var(--color-4);
}
.input_wrapper {
  display: flex;
  gap: 5px;
}
.input {
  border-radius: 10px;
  height: 40px;
  border: solid 1px var(--color-1);
  width: 250px;
  padding: 5px;
}
.bth_show {
  background-color: var(--color-3);
  border-radius: 10px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn {
  padding: 10px;
  border-radius: 10px;
  background-color: var(--color-1);
  color: var(--color-3);
  font-weight: 600;
}

@media (any-hover: hover) {
  .btn:hover,
  .bth_show:hover {
    opacity: 0.8;
    cursor: pointer;
  }
  .btn:active,
  .bth_show:active {
    opacity: 0.5;
  }
}
