.notes_wrapper {
  position: relative;
}

.textarea {
  border: 1px solid var(--color-1);
  border-radius: 10px;
  padding: 10px 10px 45px 10px;
  resize: none;
  width: 100%;
}
.notes_save_btn {
  background-color: var(--color-1);
  color: var(--color-3);
  padding: 4px 10px;
  border-radius: 10px;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

@media (any-hover: hover) {
  .notes_save_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.notes_save_btn:active {
  opacity: 0.5;
}
