.page_wrapper {
  padding: 55px 2px 30px 3px;
}
.item_wrapper {
  display: grid;
  grid-template-columns: 120px 40px 40px 40px 40px 40px;
  gap: 4px;
  background-color: var(--color-4);

  border-radius: 5px;
  margin-bottom: 10px;
  position: relative;
}

.viewTime {
  position: absolute;
  top: -7px;
  left: 0;
  font-size: 12px;
  line-height: 1;
  padding-left: 3px;
  padding-right: 3px;
  border-radius: 3px;
  background-color: var(--color-3);
}

.fullName {
  display: flex;
  align-items: center;
  padding-left: 5px;
  padding-right: 10px;
  font-size: 13px;
}
.counter_item_wrapper {
  border: solid 1px var(--color-1);

  background-color: var(--color-3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  padding-top: 5px;
  height: 100%;
  min-width: 30px;
}
.counter_item_wrapper_noActive {
  opacity: 0.2;
}
.value {
  font-size: 12px;
}

/* ------ result ------------ */
.result_wrapper {
  background-color: var(--color-1);
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-radius: 5px;
  display: flex;
  gap: 4px;
  margin-bottom: 10px;
}
.result_item_wrapper {
  border: solid 1px var(--color-1);
  border-radius: 5px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 5px;

  background-color: var(--color-3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-width: 40px;
  height: 50px;
  position: relative;
}

.result_item_count {
  position: absolute;
  top: 0;
  left: 3px;
  font-size: 11px;
  line-height: 1;
}
/* ------ /result ------------ */

@media (any-hover: hover) {
  .item_wrapper:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.item_wrapper:active {
  opacity: 0.5;
}
