.addMenu_wrapper {
  padding: 60px 5px 20px 5px;
}
.header_wrapper {
  background-color: var(--color-2);
  height: 50px;
  display: flex;
  align-items: center;
  padding: 2px 10px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header_back_btn {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16px;
  font-weight: 600;
}

.addMenuDefault_btn {
  padding: 10px;
  background-color: var(--color-6);
  color: var(--color-3);
  font-weight: 600;
  border-radius: 10px;
  width: 100%;
}

@media (any-hover: hover) {
  .addMenuDefault_btn:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.addMenuDefault_btn:active,
.header_back_btn:active {
  opacity: 0.5;
}
