.button {
  padding: 10px 16px;
  background-color: #4f46e5;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
}

@media (any-hover: hover) {
  .button:hover {
    background-color: #4338ca;
  }
}

.button:active {
  transform: scale(0.97);
}
