.page_wrapper {
  padding: 60px 5px 30px 5px;
}
.activity_info_wrapper {
  border-radius: 5px;
  border: solid 1px var(--color-1);
  background-color: var(--color-4);
  margin-bottom: 7px;
  padding-left: 5px;
  padding-right: 5px;
}
.activity_info_topBlock {
  display: flex;
  align-items: center;
  gap: 10px;
}
.userAgent {
  font-size: 10px;
}

@media (any-hover: hover) {
  .item_wrapper:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.item_wrapper:active {
  opacity: 0.5;
}
