.simila_data_wrappe {
  position: absolute;
  top: 105%;
  left: 0;
  width: 100%;
  background-color: var(--color-3);
  height: 158px;
  border-radius: 10px;
  border: 2px solid var(--color-1);
  z-index: 2;
}

.simila_data_items_wrapper {
  overflow-y: auto;
  height: 100%;
  padding: 10px;
}

.simila_data_close_btn {
  position: absolute;
  top: 5px;
  right: 5px;
}

/* --------------------- */

@media (any-hover: hover) {
  .simila_data_close_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.simila_data_close_btn:active {
  opacity: 0.5;
}
