.content_empty {
  display: flex;
  justify-content: center;
  margin-top: 80px;
}
.loader_wrapper {
  position: relative;
  margin-top: 50px;
}
.menu_wrapper {
  padding: 60px 10px 10px 10px;
  position: relative;
}

.meal_wrapper {
  border-radius: 10px;
  background-color: var(--color-5);
  overflow: hidden;
  margin-bottom: 20px;
}
.meal_wrapper_twoServings {
  background-color: var(--color-17);
}
.meal_header {
  min-height: 50px;
  background-color: var(--color-1);
  display: flex;
  align-items: center;
  padding: 10px;
  color: var(--color-3);
  font-size: 16px;
}
.meal_content_wrapper {
  padding: 10px;
}
.meal_title {
  font-weight: 600;
}
.meal_item_wrapper {
  background-color: var(--color-3);
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
}
.meal_item_wrapper_twoServings {
  background-color: var(--color-2);
}
.meal_text {
  margin-bottom: 15px;
}

.ingredients_li_modal {
  color: var(--color-6);
  text-decoration: underline;
  line-height: 2;
}
.ingredient_li {
  margin-bottom: 2px;
  padding-top: 3px;
  padding-bottom: 3px;
}
.ingredient_li_title {
  list-style-type: none;
  font-weight: 600;
  margin-left: -15px;
  margin-top: 7px;
}

.menu_btn_wrapper {
  position: sticky;
  bottom: 20px;
  left: 10px;
  z-index: 10;
  display: flex;
  gap: 10px;
  justify-content: space-between;
  background-color: var(--color-7);
  padding: 5px;
  border-radius: 10px;
}
.menu_btn {
  color: var(--color-3);
  padding: 5px;
  font-weight: 600;
  font-size: 16px;
  border-radius: 10px;
}
.delet_menu_btn {
  background-color: var(--color-10);
}
.edit_menu_btn {
  background-color: var(--color-11);
}
.clone_menu_btn {
  background-color: var(--color-6);
}

/* ----------------- modal -------------- */
.modal_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
.modal_content_wrapper {
  background-color: var(--color-3);
  padding: 70px 20px 20px 5px;
  border-radius: 10px;
  width: 80%;
  min-height: 300px;
  max-height: 100vh;
  overflow-y: auto;
  position: relative;
}
.modal_clone_content_wrapper {
  padding-left: 20px;
}
.modal_close_btn {
  background-color: var(--color-11);
  color: var(--color-3);
  padding: 5px 10px;
  border-radius: 10px;
  position: absolute;
  top: 20px;
  right: 20px;
}
.modal_li {
  margin-bottom: 5px;
  font-size: 16px;
}
.modal_clone_groups_loading,
.modal_clone_groups_wtapper {
  max-height: 300px;
  overflow: auto;
}

.modal_clone_group {
  background-color: var(--color-4);
  margin-bottom: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
}

.modal_clone_group_selected {
  width: 13px;
  flex-basis: 13px;
  flex-shrink: 0;
  height: 13px;
  border-radius: 50%;
  border: solid 1px var(--color-1);
  background-color: var(--color-3);
}
.modal_clone_group_selected_active {
  background-color: var(--color-1);
}

/* ----------------------------- */
.numberDay_wrapper {
  display: flex;
  gap: 20px;
  padding: 5px 10px;
  align-items: center;
  background-color: var(--color-7);
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 30px;
}

.input_NumberDay {
  height: 35px;
  width: 100px;
  border-radius: 10px;
  padding: 5px 10px;
  border: solid 1px var(--color-1);
}
.training_btn {
  padding: 10px;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 10px;
  background-color: var(--color-12);
  font-weight: 600;
}
.training_btn.training_btn_active {
  background-color: var(--color-2);
  color: var(--color-3);
}
/* ----------------------------- */
/* ----------------- /modal -------------- */
@media (any-hover: hover) {
  .menu_btn:hover,
  .getHistori_btn:hover,
  .modal_clone_group:hover,
  .ingredients_li_modal:hover,
  .modal_close_btn:hover,
  .interaction_item:hover,
  .header_back_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}
.getHistori_btn:active,
.modal_clone_group:active,
.menu_btn:active,
.modal_close_btn:active,
.ingredients_li_modal:active,
.interaction_item:active,
.header_back_btn:active {
  opacity: 0.5;
}
