.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px;

  background: rgba(0, 0, 0, 0.6);

  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup {
  position: relative;
  background: white;
  width: 100%;
  max-width: 370px;
  padding: 20px;
  border-radius: 12px;
  position: relative;
}

.header {
  margin: 0 0 15px 0;
  font-size: 20px;
  font-weight: 600;
}

.field {
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
}

.input,
.textarea {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.textarea {
  height: 90px;
  resize: none;
}
/* ------ */
.fileInput {
  padding: 10px;
  border: 2px dashed #ccc;
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
}

.fileInput:hover {
  border-color: #4a90e2;
}
/* ------ */

.buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.uploadBtn {
  background: #007bff;
  color: white;
  padding: 8px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.uploadBtn:hover {
  background: #0062d8;
}

.cancelBtn {
  background: #ccc;
  padding: 8px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
.preview {
  width: 100%;
  margin-bottom: 15px;
}
