.header_wrapper {
  background-color: var(--color-2);
  padding-left: 15px;
  padding-right: 15px;
  height: 50px;
  display: flex;
  align-items: center;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.content_wrapper {
  padding: 10px;
  padding-top: 60px;
}

.link_group_wrapper {
  display: flex;
  gap: 20px;
  margin-bottom: 10px;
}
.link_group_btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  height: 35px;
  background-color: var(--color-17_1);
  font-weight: 600;
  color: var(--color-3);
}
.link_group_btn_app {
  background-color: var(--color-17_2);
}

.group_links_wrapper {
  margin-bottom: 10px;
  max-height: 150px;
  overflow: auto;
  padding: 5px 5px 0px 5px;
  border: 2px solid var(--color-1);
  border-radius: 10px;
}

.btn_link {
  display: block;
  background-color: var(--color-1);
  margin-bottom: 5px;
  color: var(--color-3);
  padding: 10px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 7px;
}
.site_btn {
  background-color: var(--color-2);
}

@media (any-hover: hover) {
  .link_group_btn:hover,
  .btn_link:hover,
  .group_link:hover {
    opacity: 0.8;
    cursor: pointer;
  }
  .link_group_btn:active,
  .btn_link:active,
  .group_link:active {
    opacity: 0.5;
  }
}
