.wrapper {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
}
.title {
  font-size: 16px;
  margin-bottom: 15px;
  font-weight: 600;
}
.switch {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.input {
  display: none;
}

.slider {
  width: 52px;
  height: 22px;
  background-color: #ccc;
  border-radius: 999px;
  position: relative;
  transition: background-color 0.3s;
}

.slider::before {
  content: '';
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 50%;
  top: 2px;
  left: 2px;
  transition: transform 0.3s;
}

.input:checked + .slider {
  background-color: #4caf50;
}

.input:checked + .slider::before {
  transform: translateX(30px);
}

.text {
  font-size: 14px;
  font-weight: 500;
}
