.header_wrapper {
  background-color: var(--color-2);
  padding: 10px 15px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  width: 100%;
  z-index: 3;
}
.header_link {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16;
  font-weight: 600;
}
.content_wrapper {
  padding: 60px 10px 10px 10px;
}

.product_block {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 7px;
}

.product_info {
  padding: 10px;

  background-color: var(--color-5);
  border-radius: 10px;

  width: 100%;
}

.product_info_new {
  background-color: var(--color-1);
}

.delete_product_wrapper {
  position: relative;
  border-radius: 10px;
  height: 100%;
  background-color: var(--color-5);
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (any-hover: hover) {
  .delete_product_wrapper:hover,
  .interaction_item:hover,
  .header_link:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.delete_product_wrapper:active,
.interaction_item:active,
.header_link:active {
  opacity: 0.5;
}
