.item_wrapper {
  background-color: var(--color-1);
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 20px;
}
.item_wrapper_twoServings {
  background-color: var(--color-17);
}
.item_title {
  color: var(--color-3);
  margin-bottom: 10px;
}
.meals_container {
  /* background-color: var(--color-9); */
}
.meal_wrapper {
  padding: 10px;
  background-color: var(--color-5);
  margin-bottom: 20px;
  border-radius: 10px;
}

.input_meal_title {
  width: 100%;
  height: 40px;
  margin-bottom: 15px;
  border-radius: 5px;
}

.meal_text_textarea {
  width: 100%;
  resize: none;
  margin-bottom: 10px;
  border-radius: 5px;
}
.add_meal_btn {
  background-color: var(--color-6);
  padding: 10px;
  border-radius: 10px;
  color: var(--color-3);
}

.remove_meal_btn {
  padding: 5px 10px;
  background-color: var(--color-10);
  color: var(--color-3);
  border-radius: 10px;
}

.ingredient_wrapper {
  position: relative;
  margin-bottom: 5px;
}
.ingredient_replacement_wrapper {
  margin-left: 8px;
}

.ingredient_input {
  height: 35px;
  width: 100%;
  border-radius: 5px;
  border: 2px solid var(--color-9);
  padding-right: 75px;
}
.ingredient_input_noMacros {
  background-color: rgb(244, 214, 214);
}
.ingredient_input_title {
  border: 2px solid var(--color-6);
  color: var(--color-6);
  padding-right: 38px;
}
.ingredient_input_main {
  border: 2px solid var(--color-1);
  padding-right: 114px;
}

.ingredient_btn_wrapper {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  border-radius: 5px;
}
.ingredient_list_btn_wrapper {
  position: absolute;
  top: -63px;
  right: 0;
  height: 60px;
  display: flex;
  justify-content: space-between;
  width: 150px;
  border: 2px solid var(--color-7);
  border-radius: 5px;
  z-index: 2;
  background-color: var(--color-3);
  padding: 10px;

  box-shadow: 0px -3px 0px 3px #00000085;
}
.ingredient_list_element {
  width: 15px;
  height: 15px;
  background-color: var(--color-3);
  border-bottom: 2px solid var(--color-7);
  border-right: 2px solid var(--color-7);
  position: absolute;
  bottom: -14px;
  right: 52px;
  transform: rotate(45deg) translateY(-50%);
}
.ingredient_btn_minus {
  background-color: var(--color-10);
  height: 100%;
  width: 35px;
  border-radius: 5px;
  color: var(--color-3);

  line-height: 1;
}

.ingredient_btn {
  background-color: var(--color-10);
  height: 100%;
  min-height: 35px;
  width: 35px;
  border-radius: 5px;
  color: var(--color-3);

  line-height: 1;
}
.ingredient_main_btn_wrapper {
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  gap: 10px;
}

.ingredient_btn_menu {
  background-color: var(--color-7);
  margin-left: 3px;
}

.ingredient_btn_plus_main {
  background-color: var(--color-9);
  height: 100%;
  width: 35px;
  border-radius: 5px;
  color: var(--color-3);

  line-height: 1;
}
.plus_ingredient_title_btn {
  background-color: var(--color-6);
}
.plus_ingredient_btn {
  background-color: var(--color-1);
}
.ingredient_btn_minus_main {
  background-color: var(--color-10);
}

.ingredient_btn_basket_main {
  background-color: var(--color-3);
  border: 1px solid var(--color-1);
  height: 100%;
  width: 35px;
  border-radius: 5px;

  line-height: 1;
  margin-left: 3px;
}

.ingredient_btn_basket_main:active,
.remove_meal_btn:active,
.add_meal_btn:active,
.ingredient_btn_plus_main:active,
.ingredient_btn:active,
.ingredient_btn_minus:active {
  opacity: 0.5;
}

@media (any-hover: hover) {
  .remove_meal_btn:hover,
  .ingredient_btn_basket_main:hover,
  .add_meal_btn:hover,
  .ingredient_btn_plus_main:hover,
  .ingredient_btn:hover,
  .ingredient_btn_minus:hover {
    opacity: 0.7;
    cursor: pointer;
  }
}
