.checkbox {
  width: 25px;
  height: 25px;
  border: 1px solid var(--color-1);
  background-color: var(--color-3);
  border-radius: 5px;
}
.checkbox_active {
  background-color: var(--color-1);
}

@media (any-hover: hover) {
  .checkbox:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.checkbox:active {
  opacity: 0.5;
}
