.content_wrapper {
  padding: 60px 15px 30px 15px;
}

.popup_input {
  width: 100%;
  background-color: var(--color-12);
  border: none;
  height: 37px;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 5px;
}
.textarea {
  border: 1px solid var(--color-1);
  border-radius: 10px;
  padding: 10px 10px 45px 10px;
  resize: none;
  width: 100%;
  margin-bottom: 20px;
}
.save_btn {
  background-color: var(--color-6);
  color: var(--color-3);
  padding: 5px 20px;
  border-radius: 10px;
  margin-top: 10px;
}

@media (any-hover: hover) {
  .save_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.save_btn:active {
  opacity: 0.5;
}
