/* --------- popup ----------- */
.popup_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-3);
  z-index: 103;
  overflow-y: auto;
}
.popup_close_btn {
  background-color: var(--color-1);
  fill: var(--color-3);
  display: flex;
  padding: 3px;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  position: fixed;
  top: 15px;
  right: 20px;
}

.popup_content_wrapper {
  padding: 50px 10px 20px 10px;
}
/* ----------------------------------- */
.popup_client_fullName {
  margin-bottom: 30px;
}
.compare_wrapper {
  display: grid;
  grid-template-rows: 20px 20px repeat(8, 30px);
  grid-template-columns: min-content repeat(3, auto);
  /* grid-template-columns: min-content auto auto 60px; */
  gap: 2px;
  grid-auto-flow: column;
  font-size: 12px;
  align-items: center;
  margin-bottom: 30px;
}

.body_item_title {
  white-space: nowrap;
  padding-right: 5px;
}
.body_element {
  padding-left: 5px;
  padding-right: 5px;
  border: solid 1px var(--color-1);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.body_element.plus {
  font-weight: 600;
  color: green;
}
.body_element.minus {
  color: red;
  font-weight: 600;
}
.body_element_active_color {
  background-color: var(--color-13);
}
/* --------- popup ----------- */

@media (any-hover: hover) {
  .popup_close_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.popup_close_btn:active {
  opacity: 0.5;
}
