.header_wrapper {
  background-color: var(--color-2);
  padding: 10px 10px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header_link {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16;
  font-weight: 600;
}
.content_wrapper {
  padding: 15px;
}
.content_title {
  font-size: 16px;
  margin-bottom: 20px;
}
.group_access_wrapper {
  border: 1px solid var(--color-1);
  padding: 10px;
  border-radius: 10px;
}

.group_id_wrapper {
  position: relative;
  display: flex;
  gap: 5px;
  margin-bottom: 17px;
}
.group_access_selected_wrapper {
  display: flex;
  align-items: center;
  gap: 15px;
}
.group_access_selected_btn {
  width: 30px;
  height: 30px;
  border: 1px solid var(--color-1);
  border-radius: 5px;
}
.group_access_selected_btn_active {
  background-color: var(--color-1);
}

.alertCopyTrue {
  position: absolute;
  top: 0px;
  left: 70px;
  background-color: var(--color-1);
  color: var(--color-3);
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 3px;
}

.btn_link {
  display: block;
  background-color: var(--color-1);
  margin-bottom: 10px;
  color: var(--color-3);
  padding: 15px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 16px;
  font-size: 16;
  font-weight: 600;
}
.addLinksGroups_wrapper {
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  border: 1px solid var(--color-1);
  padding: 10px;
}
.popup_input {
  width: 100%;
  background-color: var(--color-12);
  border: none;
  height: 37px;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 5px;
}
.save_btn {
  background-color: var(--color-6);
  color: var(--color-3);
  padding: 5px 20px;
  border-radius: 10px;
}

.selected_date_wrapper {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
.selected_date_input {
  height: 40px;
  padding: 10px;
  border-radius: 10px;
}

.selected_date_text_noActive {
  color: var(--color-10);
}
.selected_date_input_noActive {
  border: 2px solid var(--color-10);
  color: var(--color-10);
}

.setting_btn_wrapper {
  display: flex;
  align-items: center;
}

@media (any-hover: hover) {
  .group_access_selected_btn:hover,
  .save_btn:hover,
  .group_id:hover,
  .header_link:hover,
  .setting_btn_wrapper:hover,
  .btn_link:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .group_access_selected_btn:active,
  .group_id:active,
  .save_btn:active,
  .setting_btn_wrapper:active,
  .header_link:active,
  .btn_link:active {
    opacity: 0.5;
  }
}
