.header_wrapper {
  background-color: var(--color-2);
  padding: 10px 15px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-3);
  font-weight: 600;
  font-size: 16px;
  position: fixed;
  width: 100%;
  z-index: 3;
}
.header_link {
  color: var(--color-3);
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: underline;
  font-size: 16;
  font-weight: 600;
}
.content_wrapper {
  padding: 60px 10px 10px 10px;
}

.ingredient_block {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 7px;
}

.ingredient_info {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;

  background-color: var(--color-5);
  border-radius: 10px;

  width: 100%;
}

.ingredient_info_new {
  background-color: var(--color-1);
}

.delete_ingredient_wrapper {
  position: relative;
  border-radius: 10px;
  height: 100%;
  background-color: var(--color-5);
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.accessGroup_item {
  background-color: var(--color-3);
  margin-top: 7px;
  padding: 3px 10px 3px 10px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accessGroups_item_active {
  width: 14px;
  height: 14px;
  background-color: var(--color-1);
  border-radius: 50%;
}

@media (any-hover: hover) {
  .delete_ingredient_wrapper:hover,
  .popupGroupsList_close_btn:hover,
  .popupGroupsList_item:hover,
  .accessGroup_delete_btn:hover,
  .ingredient_info:hover,
  .popup_close_btn:hover,
  .interaction_item:hover,
  .popup_add_btn:hover,
  .header_link:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.delete_ingredient_wrapper:active,
.popupGroupsList_close_btn:active,
.popupGroupsList_item:active,
.accessGroup_delete_btn:active,
.ingredient_info:active,
.popup_close_btn:active,
.popup_add_btn:active,
.interaction_item:active,
.header_link:active {
  opacity: 0.5;
}
