.content_wrapper {
  padding: 60px 15px 30px 15px;
}

.note {
  padding: 16px;
  border-radius: 12px;
  background-color: #f7f9fc;
  border: 1px solid #e2e6f0;
  margin-bottom: 30px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

.title {
  margin: 0 0 12px;
  font-size: 20px;
  font-weight: 600;
  color: #1f2937;
}

.list {
  margin: 0;
  padding-left: 18px;
  color: #374151;
}

.list li {
  margin-bottom: 8px;
  line-height: 1.4;
}
