.content_wrapper {
  padding: 60px 15px 30px 15px;
}

.input {
  width: 100%;
  background-color: var(--color-12);
  border: none;
  height: 37px;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 5px;
}

.selected_date_wrapper {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
.selected_date_input {
  height: 40px;
  padding: 10px;
  border-radius: 10px;
}

.selected_date_text_noActive {
  color: var(--color-10);
}
.selected_date_input_noActive {
  border: 2px solid var(--color-10);
  color: var(--color-10);
}
.save_btn {
  background-color: var(--color-6);
  color: var(--color-3);
  padding: 5px 20px;
  border-radius: 10px;
  margin-top: 10px;
}

@media (any-hover: hover) {
  .save_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.save_btn:active {
  opacity: 0.5;
}
