.notes_wrapper {
  color: var(--color-3);
  position: relative;
  margin: 5px;
  padding-top: 50px;
  padding-bottom: 30px;
}
.notes_item_wrapper {
  background-color: var(--color-6);
  padding: 15px 5px 5px 5px;
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
}

.delete_notes {
  padding: 7px;
  position: absolute;
  top: 0;
  right: 0;
}
.input_title {
  color: black;
  border: 1px solid var(--color-1);
  border-radius: 10px;
  height: 35px;
  padding: 5px;
  width: 100%;
  margin-bottom: 10px;
}
.textarea {
  color: black;
  border: 1px solid var(--color-1);
  border-radius: 10px;
  padding: 10px 10px 45px 10px;
  resize: none;
  width: 100%;
  overflow: auto;
}
.notes_save_btn {
  background-color: var(--color-1);
  color: var(--color-3);
  padding: 4px 10px;
  border-radius: 10px;
  position: fixed;
  bottom: 50px;
  right: 5px;
}

@media (any-hover: hover) {
  .delete_notes:hover,
  .interaction_item:hover,
  .notes_save_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.delete_notes:active,
.interaction_item:active,
.notes_save_btn:active {
  opacity: 0.5;
}
