.popup_fon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-1_1);
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup_content_wrapper {
  background-color: var(--color-3);
  height: 100%;
  width: 100%;
  padding: 10px 10px 10px 10px;
  overflow-y: auto;
}
.close_btn_wrapper {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
  position: sticky;
  top: 0;
}
.table_big_wrapper {
  height: 170px;
  background-color: var(--color-17);
  margin-bottom: 10px;
}

.input_search {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  border: 2px solid var(--color-18);
  margin-bottom: 5px;
  padding-left: 10px;
}
.input_value {
  height: 40px;
  width: 70px;
  border: 2px solid var(--color-2);
  padding: 5px;
}
.ingredients_list_wrapper {
  overflow-y: auto;
  padding: 10px 10px 30px 10px;
  border: 2px solid var(--color-18);
  border-radius: 5px;
}
.final_text {
  padding: 7px;
  min-height: 40px;
  border-radius: 5px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.final_text_basket {
  background-color: var(--color-19);
}
.final_text_menu {
  background-color: var(--color-13);
}
.onChange_value_btn {
  background-color: var(--color-2);
  width: 40px;
  color: var(--color-3);
  font-size: 20px;
}
.onChange_value_btn_minus {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.onChange_value_btn_plus {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.cancel_btn {
  border: 2px solid var(--color-2);
  width: 130px;
  color: var(--color-2);
  height: 35px;
  border-radius: 5px;
}
.add_btn {
  background-color: var(--color-2);
  width: 130px;
  color: var(--color-3);
  height: 35px;
  border-radius: 5px;
}
@media (any-hover: hover) {
  .onChange_value_btn:hover,
  .add_btn:hover,
  .cancel_btn:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}

.onChange_value_btn:active,
.add_btn:active,
.cancel_btn:active {
  opacity: 0.5;
}
