.table_wrapper {
  background-color: var(--color-17);
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
}
.table_item {
  border: 1px solid var(--color-19);
  padding: 5px 3px;
  font-size: 12px;
  color: var(--color-3);
}

.table_item_active {
  background-color: var(--color-2);
}
.table_item_total {
  background-color: var(--color-15);
}
